.testimonials {
  &__item:not(:last-child) {
    margin-bottom: 48px;

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.one-testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 24px;

    margin-bottom: 28px;

    @include onDesktop {
      margin-bottom: 32px;
    }
  }

  &__photo {
    width: 100%;
    grid-column: 2 / 4;

    border-radius: 50%;

  }

  &__name {
    margin: 0 0 4px;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    text-align: center;

    color: $main-accent;
  }

  &__role {
    font-size: 14px;
    line-height: 27px;
    text-align: center;

    color: #9fadc2;
  }

  &__review {
    text-align: center;
  }
}
